export const ageNormalHint = (affect: AgeNormal): string => {
  let hint = '';
  if (affect.eduEnhance !== 0) hint += ` ${affect.eduEnhance} `;
  if (affect.multiDeduct !== 0) hint += ` ${affect.multiDeduct}`;
  if (affect.appDeduct !== 0) hint += ` ${affect.appDeduct} `;
  return hint;
};


